.header{
  background: #FFF;
  box-shadow: 0px 4px 28px 0px rgba(29, 54, 112, 0.07);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
  &__inner{
    padding: 12.5px 0;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 3;
  }
  &__logo{
    display: block;
    width: 167px;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__action{
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  &__btn{
    margin-left: 40px;
  }
  &__call{
    display: none;
  }
  &._open{
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
    .burger{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
}

.burger{
  margin-right: 24px;
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  position: relative;
  &:hover{
    rect{
      fill: $primary;
    }
  }
  svg{
    @include centerAbsolute;
    width: 100%;
    height: auto;
    transition: inherit;
  }
  ._view2{
    opacity: 0;
  }
}

.menu{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 115px 0 40px;
  box-shadow: 0px 4px 28px 0px rgba(29, 54, 112, 0.07);
  background-color: #fff;
  &__inner{
    display: flex;
    justify-content: flex-end;
  }
  &__phone{
    display: none;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include lastM(0 80px 0 0);
    @media screen and (min-width: 1032px){
      &._big{
        margin-right: auto;
        .menu__link{
          font-size: 40px;
          line-height: 100%;
          font-weight: 500;
          @include lastM(0 0 40px 0);
        }
      }
    }
  }
  &__link{
    font-size: 20px;
    line-height: 140%;
    @include lastM(0 0 16px 0);
  }
  &__logo{
    display: block;
    width: 231px;
    margin: 80px 0 0 auto;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 1031px){
  .header{
    &__inner{
      justify-content: space-between;
      background-color: #fff;
      padding: 16px 0;
    }
    &__action{
      display: none;
    }
    &__logo{
      width: 150.75px;
    }
    &__call{
      display: flex;
    }
  }

  .menu{
    padding: 72px 0 40px;
    overflow-y: auto;
    height: calc(100 * var(--vh));
    &__inner{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &__links {
      @include lastM(0 0 16px 0);
    }
    &__phone{
      display: block;
      margin-bottom: 24px;
    }
    &__logo{
      width: 154px;
      margin: 24px 0 0;
    }
  }
}
