.guides{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 10px));
  gap: 20px;
  align-items: flex-start;
  margin-top: 40px;
}

.guide-card{
  border-radius: 20px;
  background: #F5F7F9;
  overflow: hidden;
  &.slideToggle-block--active{
    .guide-card__btn svg{
      transform: rotate(-180deg);
    }
  }
  &__show{
    padding: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 1px;
      background: #EDEEEF;
    }
  }
  &__btn{
    width: 16px;
    flex-shrink: 0;
    margin-left: 16px;
    svg{
      width: 100%;
      height: auto;
    }
  }
  .slideToggle-block__wrap{
    padding: 16px;
    @include grid100gap(16px);
  }
}

.buses__item,
.make-route,
.stations__item{
  border-radius: 12px;
  background: #FFF;
  padding: 8px 16px;
  display: flex;
  align-items: center;
}

.stations{
  @include grid100gap(8px);
  &__item{
    &:before{
      content: '';
      flex-shrink: 0;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: #FF0000;
      margin-right: 8px;
    }
  }
}

.make-route{
  color: $primary;
  justify-content: space-between;
  &:hover{
    background-color: rgba($blue,.1);
  }
  svg{
    width: 16px;
    height: auto;
    margin-left: 10px;
  }
}

.buses{
  display: flex;
  flex-wrap: wrap;
  margin-top: -8px;
  width: calc(100% + 8px);
  &__item{
    margin: 8px 8px 0 0;
  }
}

.map{
  height: 600px;
  border-radius: 64px;
  overflow: hidden;
}


@media screen and (max-width: 768px){
  .guides{
    margin-top: 24px;
    grid-template-columns: 100%;
  }
}
