@import 'vars';
html {
    width: 100%;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.page {
    font-family: $font1;
    font-weight: 400;
    color: $blue;
    padding-top: 128px;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 1032px;
    margin: 0 auto;
    padding: 0 16px;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 1031px){
    .page{
        padding-top: 104px;
    }
}
