.text68{
  font-size: 68px;
  line-height: 95%;
  font-weight: 700;
  font-family: $font2;
}

.text48{
  font-size: 48px;
  line-height: 117%;
  font-weight: 700;
  letter-spacing: -0.48px;
}

.text40{
  font-size: 40px;
  line-height: 100%;
  letter-spacing: -0.4px;
}

.text32{
  font-size: 32px;
  line-height: 125%;
  letter-spacing: -0.32px;
}

.text28{
  font-size: 28px;
  line-height: 115%;
}

.text24{
  font-size: 24px;
  line-height: 133%;
}

.text20{
  font-size: 20px;
  line-height: 140%;
}

.text16{
  font-size: 16px;
  line-height: 150%;
}

.text14{
  font-size: 14px;
  line-height: 129%;
}

.bold-text{
  font-weight: 700;
}

.medium-text{
  font-weight: 500;
}

.dark-text{
  color: $dark;
}

.gray-text{
  color: $gray;
}

.primary-text{
  color: $primary;
}

.font3{
  font-family: $font3;
}

.text{
  * {
    line-height: inherit;
  }
  b{
    font-weight: 500;
  }
  ul,
  li,
  p{
    @include lastM(0 0 24px);
  }
  ul{
    li{
      padding-left: 20px;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 7px;
        left: 0;
        width: 8px;
        height: 8px;
        background-image: url(../img/marker.svg);
        @include bgImgSize(100%);
      }
    }
  }
}


@media screen and (max-width: 1031px){
  .text68{
    font-size: 40px;
    line-height: 100%;
  }

  .text48{
    font-size: 30px;
    line-height: 133%;
    letter-spacing: -0.3px;
  }

  .text40{
    font-size: 24px;
    line-height: 133%;
    letter-spacing: -0.24px;
  }

  .text32{
    letter-spacing: -0.2px;
  }

  .text28{
    font-size: 18px;
    line-height: 133%;
  }

  .text32,
  .text24{
    font-size: 20px;
    line-height: 140%;
  }

  .text{
    ul,
    li,
    p{
      @include lastM(0 0 16px);
    }
  }
}
