.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(#1B1B1B, 0.60);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 84em;
        max-width: calc(100% - 2.5em);
        position: relative;
        z-index: 3;
        overflow: hidden;
    }
    &__scroll{
        overflow-y: auto;
        overflow-x: hidden;
        max-height: calc(100 * var(--vh));
        background-image: url(../img/modal-bg.png);
        background-repeat: no-repeat;
        background-position: left bottom 2.56em;
        background-size: 42.125em auto;
    }
    &__top{
        padding: 2.5em;
    }
    &__close {
        position: absolute;
        border-top: 0;
        top: .94em;
        right: 1.5em;
        background-color: transparent;
        width: .75em;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
}




@media screen and (max-width: 992px){
    .modal{
        &__scroll{
            overflow: unset;
            overflow-x: hidden;
        }
    }
}


