.footer{
  &__inner{
    padding: 40px;
    border-radius: 48px;
    background: #F5F7F9;
  }
  &__bottom{
    margin-top: 40px;
    padding-top: 24px;
    border-top: 1px solid #EDEEEF;
    display: flex;
    justify-content: space-between;
  }
  &__conf,
  &__hint{
    margin-left: 40px;
    color: rgba($gray,.5);
    &>*,
    p{
      line-height: inherit;
    }
  }
  &__conf{
    flex-shrink: 0;
    margin-left: 10px;
  }
}

.address{
  padding: 24px;
  border-radius: 16px;
  background: #FFF;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__location{
    .text14{
      line-height: 171%;
      margin-bottom: 4px;
    }
  }
  &__time{
    @include grid100gap(8px);
    text-align: right;
  }
}

.footer-nav{
  padding-left: 24px;
  margin-top: 64px;
  display: flex;
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include lastM(0 80px 0 0);
  }
  &__link{
    font-size: 16px;
    line-height: 150%;
    @include lastM(0 0 16px);
  }
}

.socials{
  flex-shrink: 0;
  display: flex;
  align-items: center;
  &__item{
    width: 24px;
    height: 24px;
    @include center;
    @include lastM(0 8px 0 0);
    svg{
      width: 16px;
      height: auto;
    }
    &:hover{
      path{
        fill:#2787F5;
      }
    }
  }
}


@media screen and (max-width: 1031px){
  .footer{
    .container{
      padding: 0;
    }
    &__inner{
      padding: 16px 16px 40px;
      border-radius: 24px;
    }
    &__bottom{
      flex-direction: column;
    }
    &__hint,
    &__conf{
      text-align: center;
      margin: 40px 0 0;
    }
    &__hint{
      margin-top: 24px;
    }
    &__conf{
      color: $blue;
    }
  }

  .address{
    display: block;
    border-radius: 16px;
    &__time{
      margin-top: 32px;
      text-align: left;
    }
  }

  .footer-nav{
    padding-left: 0;
    margin-top: 40px;
    justify-content: space-between;
  }

  .socials{
    justify-content: center;
    &__item{
      border-radius: 8px;
      background: #FFF;
      width: 40px;
      height: 40px;
      path{
        fill:#2787F5;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .footer-nav{
    flex-direction: column;
    align-items: center;
    justify-content: unset;
    &__links{
      align-items: center;
      @include lastM(0 0 32px);
    }
  }
}
