.about{
  overflow: hidden;
  .text-block{
    margin-top: 64px;
    padding-top: 64px;
    border-top: 1px solid rgba(#A7A9AC,.5);
  }
}

.text-block{
  &__grid{
    margin-top: 40px;
  }
}

.advantages{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 15px));
    gap: 20px;
    margin-top: 40px;
  }
  &__desc{
    margin-top: 40px;
    max-width: 745px;
    a{
      text-decoration: underline;
    }
  }
}

.advantage-card{
  position: relative;
  &:before{
    content: '';
    display: block;
    padding-bottom: 100%;
    width: 100%;
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    padding: 24px;
    z-index: 2;
    border-radius: 48px;
    background: #F4F7F9;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__icon{
    width: 60px;
    height: 60px;
    @include center;
    background-color: #fff;
    border-radius: 50%;
    img{
      width: 28px;
    }
  }
}

.gallery-card{
  &__img{
    display: block;
    @include imgCover(65.3%);
    img{
      border-radius: 48px;
    }
  }
  &__info{
    margin-top: 24px;
    @include grid100gap(8px);
  }
}

.info{
  &__inner{
    overflow: hidden;
    padding: 40px;
    border-radius: 48px;
    background-color: $lightBlue;
  }
  .section__nav{
    background-color: #fff;
  }
}

.info-text{
  padding-bottom: 64px;
  margin-bottom: 64px;
  border-bottom: 1px solid rgba(#A7A9AC,.5);
  &__inner{
    @include grid100gap(40px);
    max-width: 740px;
  }
}

.flats{
  overflow: unset;
  .swiper{
    &-container{
      overflow: unset;
    }
    &-slide{
      width: calc(100% / 3);
    }
  }
  .flat-card{
    height: 100%;
  }
}

.flat-card{
  padding: 24px;
  background-color: #fff;
  border-radius: 24px;
  display: flex;
  flex-direction: column;
  &__top{
    position: relative;
    .badge{
      position: absolute;
      top: -8px;
      left: -8px;
      z-index: 2;
    }
  }
  &__img{
    display: block;
    @include imgCover(67.2%);
  }
  &__prop,
  &__props{
    display: flex;
    align-items: center;
  }
  &__props{
    margin-top: 18px;
  }
  &__prop{
    font-size: 14px;
    line-height: 129%;
    color: $gray2;
    &:not(:last-child){
      &:after{
        content: '';
        margin: 0 8px;
        flex-shrink: 0;
        border-radius: 50%;
        width: 3px;
        height: 3px;
        background-color: $gray2;
      }
    }
  }
  &__title{
    margin-top: 4px;
  }
  &__prices{
    margin: 16px 0;
  }
  &__price1{
    color: $primary;
  }
  &__price2{
    text-decoration: line-through;
    color: $gray2;
  }
  &__nav{
    margin-top: auto;
  }
  &__btn{
    width: 100%;
  }
}

.have-question{
  &__inner{
    padding-bottom: 100px;
    border-bottom: 1px solid #EDEEEF;
  }
}

.question-card{
  width: 490px;
  max-width: 100%;
  margin: 0 auto;
  &__img{
    @include imgCover(61.2%);
    img{
      border-radius: 64px;
    }
  }
  &__content{
    margin-top: 40px;
    @include grid100gap(24px);
    text-align: center;
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}

.location{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 10px));
  gap: 20px;
  margin-top: 64px;
}

.location-card{
  padding: 96px 32px 24px;
  border-radius: 48px;
  background: #F4F7F9;
  text-align: center;
  &__street{
    margin-top: 32px;
  }
}

.rules{
  display: grid;
  grid-template-columns: calc(100% - 260px) 220px;
  gap: 24px 40px;
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 32px;
    }
  }
  &__text{
    @include grid100gap(8px);
  }
  &__nav{
    margin-top: 32px;
    display: flex;
    .m-btn{
      height: 41px;
      padding: 0 32px;
      &:not(:hover){
        background-color: #fff;
      }
    }
  }
}


@media screen and (max-width: 1031px){
  .about{
    .text-block{
      margin-top: 40px;
      padding-top: 40px;
    }
  }

  .text-block{
    &__title{
      br{
        display: none;
      }
    }
    &__grid{
      margin-top: 24px;
    }
  }

  .advantages{
    overflow: hidden;
    &__grid{
      margin: 24px 0 0 -16px;
      display: flex;
      gap: unset;
      @include fullScroll;
    }
    &__desc{
      margin-top: 24px;
      font-size: 14px;
      line-height: 129%;
    }
  }

  .advantage-card{
    width: 200px;
    flex-shrink: 0;
    margin-right: 16px;
    &__inner{
      border-radius: 24px;
    }
  }

  .gallery-card{
    &__img{
      padding-bottom: 100%;
      img{
        border-radius: 24px;
      }
    }
  }

  .info{
    .container{
      padding: 0;
    }
    &__inner{
      padding: 24px 16px;
      border-radius: 24px;
    }
  }

  .info-text{
    padding-bottom: 40px;
    margin-bottom: 40px;
    &__inner{
      gap: 24px;
    }
  }

  .flats{
    .swiper{
      &-slide{
        max-width: calc(100% + 20px);
        width: 300px;
      }
    }
  }

  .have-question{
    &__inner{
      padding-bottom: 40px;
    }
  }

  .question-card{
    &__content{
      margin-top: 24px;
    }
  }

  .location-card{
    border-radius: 48px;
    .text68{
      font-size: 56px;
      line-height: 142%;
    }
  }

  .rules{
    &__nav{
      margin-top: 24px;
    }
  }
}



@media screen and (max-width: 576px){
  .question-card{
    &__img{
      padding-bottom: 104%;
      img{
        border-radius: 24px;
      }
    }
  }

  .location{
    grid-template-columns: 100%;
    margin-top: 24px;
  }

  .location-card{
    padding-top: 80px;
  }

  .rules{
    grid-template-columns: 100%;
    &__img{
      order: -1;
      @include imgCover(90%);
      img{
        border-radius: 24px;
      }
    }
  }
}
