@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.page-title{
  text-align: center;
  margin-bottom: 40px;
  .text68{
    color: $primary;
  }
}

.slider{
  .swiper{
    &-container{
      position: relative;
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      position: relative;
    }
    &-button{
      position: static;
      margin: 0 0 0 16px;
    }
  }
  &__img{
    display: block;
    @include imgCover(60%);
    img{
      border-radius: 64px;
    }
  }
  &__text{
    position: absolute;
    width: calc(100% - 170px);
    left: 50%;
    transform: translateX(-50%);
    bottom: 24px;
    padding: 20px 124px 20px 32px;
    background-color: #fff;
    border-radius: 20px;
    z-index: 2;
  }
  &__nav{
    position: absolute;
    bottom: 40px;
    right: 117px;
    display: flex;
    align-items: center;
  }
}

.grid-column2{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 20px));
  gap: 40px;
}

.section{
  padding: 50px 0;
  overflow: hidden;
  &--first{
    padding-top: 0;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 64px;
  }
  &__nav{
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 64px;
    background: #F5F7F9;
    padding: 16px 24px;
    width: 295px;
  }
}

.collection-slider{
  .swiper{
    &-container{
      width: calc(100% + 20px);
      overflow: hidden;
    }
    &-slide{
      width: 50%;
      padding-right: 20px;
    }
    &-button{
      position: static;
      margin: 0 16px 0 0;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
  }
}

.badge{
  padding: 0 16px;
  border-radius: 24px;
  background-color: $blue;
  height: 40px;
  display: flex;
  align-items: center;
  color: #fff;
  font-weight: 500;
}

.slideToggle-block {
  &__show{
    cursor: pointer;
  }
  &__hidden {
    height: 0;
    overflow: hidden;
    transition: all .425s;
  }
}


@media screen and (max-width: 1031px){
  ._mob-order1{
    order: -1;
  }

  .page-title{
    margin-bottom: 24px;
  }

  .slider{
    &__text{
      width: calc(100% - 32px);
    }
    &__nav{
      right: 32px;
    }
    &__img{
      img{
        border-radius: 24px;
      }
    }
  }

  .grid-column2{
    grid-template-columns: 100%;
    gap: 16px;
  }

  .section{
    padding: 20px 0;
    &--first{
      padding-top: 0;
    }
    &__top{
      margin-bottom: 40px;
      display: block;
    }
    &__nav{
      margin: 24px 0 0;
      width: 100%;
      min-width: unset;
    }
  }

  .collection-slider{
    .swiper{
      &-container{
        width: 100%;
        overflow: unset;
      }
      &-slide{
        width: 280px;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .slider{
    .swiper{
      &-container{
        overflow: unset;
      }
      &-slide{
        display: flex;
        flex-direction: column;
      }
    }
    &__img{
      padding-bottom: 111%;
    }
    &__text{
      position: static;
      flex-grow: 1;
      width: 100%;
      transform: unset;
      border-radius: 24px;
      background: #F5F7F9;
      padding: 16px 16px 56px;
      margin-top: 16px;
    }
    &__nav{
      right: 16px;
      bottom: 16px;
    }
  }
}
